import React, { useState, useEffect } from 'react';

// --- Helper Components ---

// Icon for the plus button, using SVG for a clean look
const PlusIcon = () => (
    <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 text-gray-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
    </svg>
);

// A single app card component
// Define the type for the app object
interface App {
    id: number;
    name: string;
    description: string;
}

const AppCard = ({ app, onClick }: { app: App; onClick: (app: App) => void }) => (
    <div
        className="group relative aspect-square bg-gray-200 dark:bg-gray-700 rounded-2xl shadow-md overflow-hidden cursor-pointer transform transition-all duration-300 hover:scale-105 hover:shadow-xl"
        onClick={() => onClick(app)}
    >
        {/* You can replace this with an actual app icon or preview */}
        <div className="w-full h-full flex items-center justify-center">
            <span className="text-gray-500 dark:text-gray-400 text-lg font-semibold">{app.name}</span>
        </div>
        <div className="absolute inset-0 bg-black bg-opacity-50 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity duration-300">
            <p className="text-white text-center p-2">{app.description}</p>
        </div>
    </div>
);

// A placeholder card for adding new apps
const AddNewAppCard = ({ onClick }: { onClick: () => void }) => (
    <div
        onClick={onClick}
        className="flex items-center justify-center aspect-square bg-gray-100 dark:bg-gray-800 border-2 border-dashed border-gray-300 dark:border-gray-600 rounded-2xl cursor-pointer hover:bg-gray-200 dark:hover:bg-gray-700 transition-colors duration-300"
    >
        <PlusIcon />
    </div>
);


// --- Main App Component ---

export default function App() {
    // --- State Management ---
    const [apps, setApps] = useState<App[]>([]);
    const [username, setUsername] = useState('Username');
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [selectedApp, setSelectedApp] = useState<App | null>(null);

    // --- Mock Data ---
    // In a real application, you would fetch this data from an API.
    useEffect(() => {
        const mockApps = [
            { id: 1, name: 'App One', description: 'This is the first amazing app.' },
            { id: 2, name: 'App Two', description: 'A second app with great features.' },
            { id: 3, name: 'App Three', description: 'The third app in the collection.' },
            { id: 4, name: 'App Four', description: 'Another wonderful application.' },
            { id: 5, name: 'App Five', description: 'The final app in this showcase.' },
        ];
        setApps(mockApps);
    }, []);

    // --- Event Handlers ---
    const handleAppClick = (app: App) => {
        setSelectedApp(app);
        // In a real app, you might navigate to the app's page
        // or open a more detailed modal here.
        alert(`You clicked on ${app.name}`);
    };

    const handleAddNewApp = () => {
        // This would open a form or modal to create a new app.
        alert("Add new app functionality goes here!");
    }


    // --- Render Method ---
    return (
        <div className="min-h-screen bg-gray-50 dark:bg-gray-900 text-gray-800 dark:text-gray-200 font-sans">
            <div className="container mx-auto p-4 sm:p-6 lg:p-8">

                {/* User Profile Section */}
                <header className="flex items-center mb-8 sm:mb-12">
                    <div className="w-16 h-16 sm:w-20 sm:h-20 bg-gray-300 dark:bg-gray-600 rounded-full mr-4 sm:mr-6 shadow-md">
                        {/* You can place a user avatar image here */}
                    </div>
                    <h1 className="text-2xl sm:text-3xl lg:text-4xl font-bold tracking-tight">{username}</h1>
                </header>

                {/* Apps Section */}
                <main>
                    <h2 className="text-xl sm:text-2xl font-semibold mb-6 sm:mb-8">My Apps</h2>

                    {/* Grid for the apps */}
                    <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4 sm:gap-6">
                        {apps.map(app => (
                            <AppCard key={app.id} app={app} onClick={handleAppClick} />
                        ))}
                        <AddNewAppCard onClick={handleAddNewApp} />
                    </div>
                </main>

            </div>
        </div>
    );
}
